body {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  background: whitesmoke; }

#header {
  display: block;
  border-bottom: 4px solid #A3A0A6;
  margin-bottom: 5px;
  width: auto;
  padding: 5px; }

button {
  height: 40px;
  width: 125px;
  margin: 5px 10px 5px 0;
  font-size: 14px;
  border: none;
  border-radius: 5px;
  color: white;
  background: #4682b4;
  cursor: pointer; }

#container {
  float: left;
  width: 70vw;
  height: auto;
  margin: 10px;
  padding-left: 10px; }

.hidden {
  display: none; }

/* sidebar */
aside {
  position: relative;
  float: left;
  margin-top: 10px;
  border: 2px solid #A3A0A6;
  border-radius: 2px;
  width: 200px;
  height: 70vh;
  padding: 1%; }
  aside p {
    display: block; }

#ongoing-list {
  display: block;
  border: 1px solid #A3A0A6;
  padding: 0 5px 5px 5px;
  margin-bottom: 20px; }
  #ongoing-list #new-subtask-button {
    display: none; }

.project-entry-sidebar {
  font-size: 18px;
  cursor: pointer; }
  .project-entry-sidebar:hover {
    background: lightgrey; }

#task-form, #project-form, #subtask-form {
  z-index: 1;
  margin: 5% 10% 10% 10%;
  padding: 5px;
  width: 80%;
  height: auto;
  background: white;
  border: 3px solid gray; }
  #task-form input, #task-form select, #project-form input, #project-form select, #subtask-form input, #subtask-form select {
    margin: 10px 15px;
    height: 25px;
    width: 200px; }

/* list and calendar */
.date-title {
  text-align: center; }

.heading {
  height: 50px; }

#cal-title, #list-title {
  text-align: center; }

.back-date, .forward-date {
  display: inline-flex;
  color: grey; }
  .back-date div, .forward-date div {
    font-size: 1.5em;
    margin: 5px;
    padding: 2px 5px;
    border: 1px solid #A3A0A6;
    border-radius: 10px;
    cursor: pointer; }

.back-date {
  float: left; }

.forward-date {
  float: right; }

/* main calendar */
#calendar {
  border: 1px solid #A3A0A6;
  border-radius: 2px;
  margin: 0 20px;
  min-width: 756px;
  max-width: 966px; }

#caldendar-heading {
  display: inline-flex;
  height: 20px; }

.week, #weekdays {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-auto-flow: dense; }

#weekdays div {
  border: 1px solid black;
  background: #4682b4;
  color: white;
  height: 20px;
  width: auto;
  text-align: center; }

/* calendar days */
.calendar-day {
  width: auto;
  height: 12vh;
  border: 1px solid #A3A0A6;
  padding: 3px; }
  .calendar-day p {
    margin-top: -2px;
    width: 100%; }

#today, #selected-date {
  font-weight: bold; }

#today {
  background: #f2efc1; }

#selected-date {
  background: #4682b4;
  color: white; }

.diff-month {
  background: darkgray;
  border: 1px solid rgba(28, 29, 29, 0.418); }

/* list view */
#list-view {
  max-width: 966px;
  margin: 0 20px; }

#list-heading {
  height: 50px; }

.project-list {
  margin: 0 20px; }
  .project-list h3 {
    margin-bottom: 2px; }

.project-entry {
  font-size: 18px;
  margin-bottom: 10px; }

.task-entry {
  margin-left: 20px; }

.title {
  width: auto;
  cursor: pointer; }
  .title:hover {
    background: lightgrey; }

/* tasks and projects */
.task-div, .project-div {
  font-size: 12px;
  padding: 2px 5px;
  width: auto;
  margin: 2px 0;
  color: cornsilk;
  border-radius: 3px; }

.project-div {
  border: 2px solid black; }

.low {
  background: rgba(0, 128, 0, 0.712); }

.medium {
  background: #d1892b; }

.high {
  background: rgba(206, 35, 35, 0.781); }

.dot {
  float: left;
  height: 10px;
  width: 10px;
  margin: 5px 10px 0 0;
  border-radius: 5px; }

/* modals */
.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: rgba(129, 124, 124, 0.8); }

.modal-content {
  background: white;
  font-size: 14px;
  color: black;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  height: 50%;
  z-index: 3; }

.close-modal {
  float: right;
  font-size: 20px;
  background: darkgray;
  color: white;
  border-radius: 15px;
  text-align: center;
  width: 30px;
  height: 30px;
  margin: -25px -25px 0 0;
  z-index: 5;
  cursor: pointer; }

.checklist li {
  list-style-type: none;
  border: 1px solid lightgray;
  width: 250px;
  margin-left: -10px;
  padding: 5px 5px; }
